package io.writeopia.app.sql

import app.cash.sqldelight.Query
import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlCursor
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.String

public class NotesConfigurationEntityQueries(
  driver: SqlDriver,
) : SuspendingTransacterImpl(driver) {
  public fun <T : Any> selectConfigurationByUserId(user_id: String, mapper: (
    user_id: String,
    arrangement_type: String,
    order_by: String,
  ) -> T): Query<T> = SelectConfigurationByUserIdQuery(user_id) { cursor ->
    mapper(
      cursor.getString(0)!!,
      cursor.getString(1)!!,
      cursor.getString(2)!!
    )
  }

  public fun selectConfigurationByUserId(user_id: String): Query<NotesConfiguration> =
      selectConfigurationByUserId(user_id) { user_id_, arrangement_type, order_by ->
    NotesConfiguration(
      user_id_,
      arrangement_type,
      order_by
    )
  }

  public suspend fun insert(
    user_id: String,
    arrangement_type: String,
    order_by: String,
  ) {
    driver.execute(-1_935_014_154, """
        |INSERT INTO notesConfiguration(user_id, arrangement_type, order_by)
        |VALUES (?, ?, ?)
        |ON CONFLICT(user_id) DO
        |UPDATE SET user_id=excluded.user_id, arrangement_type=excluded.arrangement_type, order_by=excluded.order_by
        """.trimMargin(), 3) {
          bindString(0, user_id)
          bindString(1, arrangement_type)
          bindString(2, order_by)
        }.await()
    notifyQueries(-1_935_014_154) { emit ->
      emit("notesConfiguration")
    }
  }

  private inner class SelectConfigurationByUserIdQuery<out T : Any>(
    public val user_id: String,
    mapper: (SqlCursor) -> T,
  ) : Query<T>(mapper) {
    override fun addListener(listener: Query.Listener) {
      driver.addListener("notesConfiguration", listener = listener)
    }

    override fun removeListener(listener: Query.Listener) {
      driver.removeListener("notesConfiguration", listener = listener)
    }

    override fun <R> execute(mapper: (SqlCursor) -> QueryResult<R>): QueryResult<R> =
        driver.executeQuery(1_482_084_570, """
    |SELECT *
    |FROM notesConfiguration
    |WHERE notesConfiguration.user_id = ?
    """.trimMargin(), mapper, 1) {
      bindString(0, user_id)
    }

    override fun toString(): String = "NotesConfigurationEntity.sq:selectConfigurationByUserId"
  }
}
